import React, { Component } from "react";
import SyntaxHighlighter from 'react-syntax-highlighter';
// import { zenburn } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import { agate } from 'react-syntax-highlighter/dist/esm/styles/hljs';



let snippet1 = `import * as handTrack from 'handtrackjs'; 

const img = document.getElementById('img'); 

handTrack.load().then(model => {  
    model.detect(img).then(predictions => {
        console.log('Predictions: ', predictions); 
    });
});



`


let snippet2 = `import * as handTrack from 'handtrackjs'; 

const img = document.getElementById('img'); 
const canvas = document.getElementById("canvas");
const ctx = canvas.getContext("2d");

handTrack.load().then(model => {  
    model.detect(img).then(preds => { 
      model.renderPredictions(preds, canvas, ctx, img);
    });
});

`

let snippet3 = `import * as handTrack from 'handtrackjs';

handTrack.startVideo(video).then(function (status) {
    if (status) {
        console.log("Video started")
    } else {
        console.log("Please enable video")
    }
});

// handTrack.stopVideo(); 

`

const JSSnippet1 = () => {

    return <SyntaxHighlighter language='javascript' style={agate}>{snippet1}</SyntaxHighlighter>;
}

const JSSnippet2 = () => {

    return <SyntaxHighlighter language='javascript' style={agate}>{snippet2}</SyntaxHighlighter>;
}

const JSSnippet3 = () => {

    return <SyntaxHighlighter language='javascript' style={agate}>{snippet3}</SyntaxHighlighter>;
}

class Usage extends Component {
    render() {
        //  
        return (
            <div className="">
                <div className="pagetitle mt10">Usage Examples </div>
                <br />
                <div className="flex">
                    <div className="flex4 mr10 ">
                        <div className="bluehightlight mb10 boldtext">
                            load Model
                        </div>
                        <JSSnippet1 />

                    </div>
                    <div className="flex4 mr10 ">
                        <div className="bluehightlight mb10 boldtext">
                            Render Predictions
                        </div>
                        <JSSnippet2 />

                    </div>
                    <div className="flex4 mr10 ">
                        <div className="bluehightlight mb10 boldtext">
                            Video Control
                        </div>
                        <JSSnippet3 />

                    </div>
                </div>

                {/* <Markdown source={snippet1}></Markdown> */}


            </div>

        );
    }
}

export default Usage;